  function getStartIndices(sentence, segments) {
    let start = 0;
    const result = [];
  
    for (let segment of segments) {
      result.push(start);
      start += segment.length;
    }
  
    return result;
  }


  const splitIntoWords = (text, maxLineLength) => {
    const words = text.split(" ");
    const lines = [];

    let currentLine = words[0];
    
  
    for (let i = 1; i < words.length; i++) {
      //console.log(words[i].length + " " + words[i] + " " );
      
      if (currentLine.length + words[i].length <= maxLineLength) {
        currentLine += " " + words[i];
      } else {
        currentLine += " ";
        lines.push(currentLine);
        currentLine = words[i];
      }
    }

    lines.push(currentLine); // add the last line

    return lines;
  };
  
  const findNthWordIndex = (str, n) => {
    // Split the string into an array of words
    const words = str.split(' ');
  
    // Get the nth word
    const nthWord = words[n - 1];
  
    // Find the index of the nth word in the original string
    const index = str.indexOf(nthWord);
  
    return index;
  }
  

